<template>
  <div v-if="objURL">
    <v-img v-if="isImage" :src="objURL"/>
    <a v-else :download="data.title" :href="objURL">{{ data.title }}</a>
  </div>
</template>
<script>
export default {
  props: ["data", "code"],
  data: function() {
    return {
      objURL: "",
    }
  },
  computed: {
    isImage: function() {
      return this.data.contentType && this.data.contentType.startsWith("image/")
    },
    attachmentCode: function () {
      return this.code
    }
  },
  methods: {
    setObjectURL() {
      if ( this.data.data && this.data.contentType ) {
        let dataURL = "data:"+this.data.contentType+";base64,"+this.data.data
        fetch(dataURL).then( res => res.blob() ).then( blob => this.objURL = URL.createObjectURL( blob ) ).catch( e => {
          console.log("Failed to get data from base64.",e)
        } )
      }
    }
  },
  created() {
    this.setObjectURL()
  }
}
</script>